window.EVENT_NUM = 0;

window.getEventNum = () => {
    EVENT_NUM++;
    return 'EVENT_' + EVENT_NUM;
};

window.EventConst = {};
EventConst.RESIZE = getEventNum();          // 页面resize
EventConst.TICK = getEventNum();            // 刷新时间
EventConst.FILE_END = getEventNum();        // 单个文件加载结束（不对外）
EventConst.LOAD_END = getEventNum();        // 所有文件加载结束（不对外）
EventConst.SILENCE_LOAD_END = getEventNum();// 所有文件静默加载结束（不对外）
EventConst.SCENE_LOAD_END = getEventNum();  // 场景资源静默加载结束（不对外）
EventConst.RES_PROGRESS = getEventNum();    // 资源加载进度变更（对外）
EventConst.RES_READY = getEventNum();       // 所有资源加载结束（对外）
EventConst.SILENCE_RES_READY = getEventNum(); // 文件静默加载结束（对外）
EventConst.SCENE_RES_READY = getEventNum(); // 场景资源静默加载结束（对外）
EventConst.CAMERA_ROTATE = getEventNum();   // 相机旋转

EventConst.SHOOT = getEventNum();           // 圈射出
EventConst.RESET = getEventNum();           // 圈重置

// egret threejs通信事件
window.BridgeEventConst = {};
// trigger
BridgeEventConst.LOAD_PROGRESS_CHANGE = 'load_progress_change'; // 资源加载进度 {current: 0, total: 44}
BridgeEventConst.SCENE_LOAD_END = 'scene_load_end'; // 静默加载场景资源成功
BridgeEventConst.POWER_CHANGE = 'power_change'; // 力道改变 power 0-1
BridgeEventConst.SHOOT = 'shoot'; // 圈发射
BridgeEventConst.SHOOT_RESULT = 'shoot_result'; // 套圈结果 name 如果为空字符串则是没套中


// on
BridgeEventConst.LOAD_3D = "load_3d"; // 开始加载3D资源 {scene: 1-3}
BridgeEventConst.SCENE_LOAD_3D = "scene_load_3d"; // 场景加载3D资源 {scene: 1-3}
BridgeEventConst.HIDE_3D = 'hide_3d'; // 隐藏3D
BridgeEventConst.SHOW_3D = 'show_3d'; // 显示3D
BridgeEventConst.CHANGE_FERRULE = 'change_ferrule'; // 更换套圈 {type: 0-2} 0-普通 1-大圈 2-必中
BridgeEventConst.CHANGE_SCENE = 'change_scene'; // 切换场景 {type: 1-3} 0-母婴 1-食品 2-个护
BridgeEventConst.AWAKE = 'awake'; // 唤起游戏
BridgeEventConst.FORCE_STOP = 'force_stop'; // 强行结束 退出游戏等操作需隐藏套圈
BridgeEventConst.LOCK = 'lock'; // 禁止操作
BridgeEventConst.UNLOCK = 'unlock'; // 允许操作
BridgeEventConst.REFRESH_PRODUCTS = 'refresh_products'; // 刷新道具
