import * as THREE from 'three'
import ObjectBase from './base/ObjectBase';

export default class ConveyorBelt extends ObjectBase {
    constructor(_options) {
        super(_options);

        this.index = _options.index + 1; // 1-4

        this.bname = 'ConveyorBelt_' + this.index;

        this.setModel();
        this.setBody();
    }

    setModel() {
        this.positionArr = [3.81, 2.49, 1.22, -0.055];

        const modelName = 'conveyorBelt' + this.index;
        this.model = this.resource.items[modelName].scene.clone();
        this.model.scale.setScalar(0.01);
        this.container.position.z = this.positionArr[this.index - 1];
        this.container.add(this.model);

        this.container.updateWorldMatrix();
        this.setBakedMaterial();
    }

    setBakedMaterial() {
        const baseBakedTexture = this.resource.items[`converyorBeltBase${this.index}BakedTexture`];
        baseBakedTexture.flipY = false;
        baseBakedTexture.encoding = THREE.sRGBEncoding;
        const baseBakedMaterial = new THREE.MeshBasicMaterial({
            map: baseBakedTexture
        })

        const sepBakedTexture = this.resource.items[`converyorBeltSep${this.index}BakedTexture`];
        sepBakedTexture.flipY = false;
        sepBakedTexture.encoding = THREE.sRGBEncoding;
        const sepBakedMaterial = new THREE.MeshBasicMaterial({
            map: sepBakedTexture
        })
        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                if (child.name.indexOf('Dummy') > -1) {
                    child.visible = false;
                } else if (child.name.indexOf('Sep') > -1) {
                    child.material = sepBakedMaterial;
                } else if (child.name.indexOf('Base') > -1) {
                    child.material = baseBakedMaterial;
                }
            }
        });
    }
}