import * as THREE from 'three'
import * as CANNON from 'cannon-es';
import ObjectBase from './base/ObjectBase';

export default class Ground extends ObjectBase {
    constructor(_options) {
        super(_options);

        this.bname = 'Ground';
        this.setModel();
        this.setMaterial();
        this.setBody();
    }

    setModel() {
        this.model = this.resource.items['ground'].scene;
        this.model.scale.setScalar(0.01);
        this.container.add(this.model);
    }

    setMaterial() {
        const bakedTexture = this.resource.items['groundBakedTexture'];
        bakedTexture.flipY = false;
        bakedTexture.encoding = THREE.sRGBEncoding;
        const bakedMaterial = new THREE.MeshBasicMaterial({
            map: bakedTexture
        })

        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                child.material = bakedMaterial;
            }
        });
    }

    setBody() {
        this.body = new CANNON.Body({
            mass: 0,
            shape: new CANNON.Plane()
        })
        this.body.material = this.physics.defaultMaterial;

        this.body.quaternion.setFromAxisAngle(new CANNON.Vec3(-1, 0, 0), Math.PI / 2);
        this.body.name = this.bname;
        this.physics.world.addBody(this.body);
    }
}