import * as THREE from 'three'
import Product from './base/Product';

export default class ProductsContainer {
    constructor(_options) {
        this.renderer = _options.renderer;
        this.sizes = _options.sizes;
        this.time = _options.time;
        this.resource = _options.resource;
        this.phys = _options.phys;
        this.productsArray = [];

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.setData();

        this.resource.on(EventConst.SILENCE_RES_READY, this.setAllLogoMaterial.bind(this));
    }

    setData() {
        this.posArr = [{
                x: -0.33,
                z: -0.055
            },
            {
                x: 0,
                z: -0.055
            },
            {
                x: 0.33,
                z: -0.055
            },
            {
                x: -0.488,
                z: 1.22
            },
            {
                x: -0.157,
                z: 1.22
            },
            {
                x: 0.174,
                z: 1.22
            },
            {
                x: 0.502,
                z: 1.22
            },
            {
                x: -0.307,
                z: 2.49
            },
            {
                x: 0,
                z: 2.49
            },
            {
                x: 0.307,
                z: 2.49
            },
            {
                x: -0.45,
                z: 3.81
            },
            {
                x: -0.153,
                z: 3.81
            },
            {
                x: 0.153,
                z: 3.81
            },
            {
                x: 0.45,
                z: 3.81
            },
        ]

        /**母婴 */
        this.category_1 = ['bomb', 'magicRing', 'magicBox', 'milkCan', 'diaper', 'turtleShell', 'milk', 'lego'];
        /**食品 */
        this.category_2 = ['bomb', 'magicRing', 'magicBox', 'juice', 'snacks', 'beer', 'paperBox', 'oilBottle', 'rice', 'beverage'];
        /**个护 */
        this.category_3 = ['bomb', 'magicRing', 'magicBox', 'handscream', 'napkin', 'tissueBox', 'laundry', 'lotion'];

        this.category = this.category_1;
    }

    /**
     * 设置商品
     * @param {Product[]} products 
     */
    setProducts(products) {
        const logoArray = [];
        this.productsArray.length = 0;
        products.forEach((productInfo, index) => {
            const name = productInfo.name;

            productInfo.b_name = "test";
            productInfo.b_img = "https://tstatic.ssl.jimingkeji.com.cn/jd_ar_test/brand_logo_test.jpg"
            // const name = category[index % this.category_1.length];
            const product = new Product({
                name: name,
                renderer: this.renderer,
                time: this.time,
                sizes: this.sizes,
                resource: this.resource,
                phys: this.phys,
                m_id: productInfo.m_id,
                logo: productInfo.b_name,
                index: index
            });

            if (product) {
                const pos = this.posArr[index];
                pos.y = -0.002;
                product.container.position.copy(pos);
                product.container.updateWorldMatrix();
                this.container.add(product.container);
                this.productsArray.push(product);
            }

            // push logo texture
            if (productInfo.b_name.length > 0 && !this.resource.items[productInfo.b_name]) {
                logoArray.push({
                    name: productInfo.b_name,
                    source: productInfo.b_img,
                    type: "texture"
                })
            }
        })

        // 加载logo图片
        if (logoArray.length > 0) {
            this.resource.silenceLoad(logoArray);
        } else {
            this.setAllLogoMaterial();
        }
    }

    /**
     * 更新logo图片
     */
    setAllLogoMaterial() {
        this.productsArray.map(product => {
            product.setLogoMaterial();
        })
    }

    /**
     * 刷新商品
     * @param {*} products 
     */
    refreshProducts(products) {
        this.cleanProducts();

        this.setProducts(products);
    }

    /**
     * 清理所有商品
     */
    cleanProducts() {
        while (this.productsArray.length > 0) {
            const product = this.productsArray.shift();
            product.container.traverse(child => {
                if (child instanceof THREE.Mesh) {
                    child.geometry.dispose();
                    child.material.dispose();
                }
            })
            this.container.remove(product.container);
            this.phys.world.removeBody(product.body);
        }
    }
}