import ObjectBase from "./base/ObjectBase";
import * as CANNON from 'cannon-es';
import * as THREE from 'three'
import gsap from "gsap/gsap-core";
import { to } from "gsap";

/**
 * 套圈
 */
export default class Ferrule extends ObjectBase {
    constructor(_options) {
        _options.updateMatrix = true;

        super(_options);

        this.origin = new THREE.Vector3(0, 0.31, 6.6);
        this.originRotationX = Math.PI * 0.70;
        this.mass = 1;

        this.radialSegments = 16;
        this.tubelarSegments = 32;

        this.config = [{
                radius: 0.12,
                tubeRadius: 0.008
            },
            {
                radius: 0.15,
                tubeRadius: 0.008
            },
            {
                radius: 0.12,
                tubeRadius: 0.008
            }
        ]

        this.ferruleType = CustomConst.FerruleConst.NORMAL;

        this.radius = this.config[this.ferruleType].radius;
        this.tubeRadius = this.config[this.ferruleType].tubeRadius;

        this.bodies = [];

        this.isInit = _options.isInit;
        if (this.isInit) {
            this.initStatus();
        }

        this.setModel();
        this.setBody();

    }

    initStatus() {
        this.container.position.copy(this.origin);
        this.container.rotation.x = Math.PI / 2;
    }

    applyForce(fc) {
        const force = fc || new CANNON.Vec3(0, 0, 0);
        this.circleBody.mass = this.mass;
        this.circleBody.wakeUp();
        this.circleBody.applyForce(force, this.container.position);
        this.model.rotation.y = 0;
        this.isShaking = false;

        if (!force.almostZero()) {
            const rot = {
                x: Math.PI * 0.5
            }
            gsap.to(rot, {
                x: '+=' + Math.PI * 0.2,
                duration: 0.25,
                onUpdate: () => {
                    this.circleBody.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), rot.x)
                }
            })
        }
    }

    cancelForce() {
        this.circleBody.mass = 3;
    }

    setModel(texName) {
        const textureName = texName || 'matcapNormalTexture';
        this.matcapTexture = this.resource.items[textureName];

        this.model = new THREE.Mesh(
            new THREE.TorusGeometry(this.radius, this.tubeRadius, this.radialSegments, this.tubelarSegments),
            new THREE.MeshMatcapMaterial({
                matcap: this.matcapTexture,
                transparent: true
            })
        )

        this.container.add(this.model);
    }

    setMaterial() {}

    setBody() {

        this.config.forEach(cfg => {
            const shape = new CANNON.Sphere(cfg.tubeRadius);

            let body = new CANNON.Body({
                mass: this.mass,
            })

            body.material = this.physics.defaultMaterial;
            body.sleepSpeedLimit = 0.001;

            // x2的原因是使得球相交，从而提高碰撞体的精度
            for (let i = 0; i < this.tubelarSegments * 2; i++) {
                body.addShape(
                    shape,
                    new CANNON.Vec3(
                        cfg.radius * Math.cos(Math.PI * 2 / (this.tubelarSegments * 2) * i),
                        cfg.radius * Math.sin(Math.PI * 2 / (this.tubelarSegments * 2) * i),
                        0,
                    )
                )
            }
            body.position.copy(this.container.position);
            body.quaternion.copy(this.container.quaternion);
            body.sleep();

            this.bodies.push(body);
        })

        this.circleBody = this.bodies[this.ferruleType];
        this.physics.world.addBody(this.circleBody);

        this.time.on(EventConst.TICK, this.syncContainer.bind(this));
    }

    syncContainer() {
        if (this.container && this.circleBody) {
            this.container.position.copy(this.circleBody.position);

            if (!this.isShaking) {
                this.container.quaternion.copy(this.circleBody.quaternion);
            }
        }
    }

    change(ferruleType) {
        if (this.ferruleType == ferruleType) {
            return;
        }

        this.container.remove(this.model);
        this.model.geometry.dispose();
        this.model.material.dispose();
        this.physics.world.removeBody(this.circleBody);

        this.ferruleType = ferruleType;

        this.radius = this.config[this.ferruleType].radius;
        this.tubeRadius = this.config[this.ferruleType].tubeRadius;

        if (ferruleType == CustomConst.FerruleConst.NORMAL) {
            this.setModel('matcapNormalTexture')
        } else if (ferruleType == CustomConst.FerruleConst.BIG) {
            this.setModel('matcapGoldTexture')
        } else if (ferruleType == CustomConst.FerruleConst.MUSTHIT) {
            this.setModel('matcapMustInTexture')
        }

        this.circleBody = this.bodies[this.ferruleType];
        this.circleBody.position.copy(this.container.position);
        this.circleBody.quaternion.copy(this.container.quaternion);
        this.physics.world.addBody(this.circleBody);
        this.circleBody.sleep();
    }

    /**
     * 换圈动效
     * @param {*} cb 
     * @param {*} ref 
     */
    shake(cb, ref) {
        this.isShaking = true;

        if (this.ferruleType == CustomConst.FerruleConst.BIG) {
            this.model.scale.setScalar(0.8)
        }

        if (!this.shakeTl) {
            const rot = {
                y: 0
            };
            const rotM = Math.PI;
            const rotTime = 0.1;
            const tl = gsap.timeline({
                onUpdate: () => {
                    this.model.rotation.y = rot.y;
                },
                onComplete: () => {
                    if (cb) {
                        cb.apply(ref);

                        if (this.ferruleType == CustomConst.FerruleConst.BIG) {
                            gsap.to(this.model.scale, {
                                x: 1,
                                y: 1,
                                z: 1,
                                duration: 0.35
                            })
                        }
                    }
                }
            });

            tl.fromTo(rot, {
                y: 0
            }, {
                y: rotM,
                duration: rotTime * 15,
                ease: "elastic.out(1, 0.5)"
            });

            

            this.shakeTl = tl;
        }
        this.shakeTl.restart();
    }

    moveLeft() {
        if (this.isShaking || this.isMovingLeft) {
            return;
        }

        this.isMovingRight = false;
        this.isMovingLeft = true;
        gsap.killTweensOf(this.model.rotation);
        gsap.to(this.model.rotation, {
            y: Math.PI * 0.05,
            duration: 0.5,
            ease: 'circ.out'
        })
    }

    moveRight() {
        if (this.isShaking || this.isMovingRight) {
            return;
        }

        this.isMovingLeft = false;
        this.isMovingRight = true;
        gsap.killTweensOf(this.model.rotation);
        gsap.to(this.model.rotation, {
            y: -Math.PI * 0.05,
            duration: 0.5,
            ease: 'circ.out'
        })
    }

    moveRecover() {
        this.isMovingLeft = false;
        this.isMovingRight = false;
        if (this.isShaking) {
            return;
        }

        gsap.killTweensOf(this.model.rotation);
        gsap.to(this.model.rotation, {
            y: 0,
            duration: 0.5,
            ease: 'circ.out'
        })
    }

    /**
     * 必中圈传送到特定位置
     * @param {THREE.Vector3} pos 
     */
    teleportTo(pos) {
        this.container.rotation.set(Math.PI / 2, 0, 0);

        this.circleBody.position.copy(pos);
        this.circleBody.quaternion.copy(this.container.quaternion);
        this.circleBody.wakeUp();
    }

    reset(posX) {
        posX = posX || this.origin.x;
        this.show();

        this.container.rotation.set(Math.PI / 2, 0, 0);
        this.container.position.set(posX, this.origin.y, this.origin.z);

        this.circleBody.sleep();
        this.circleBody.position.copy(this.container.position);
        this.circleBody.quaternion.copy(this.container.quaternion);
    }

    hide(dur) {
        dur = dur || 0.1;
        gsap.to(this.model.material, {
            opacity: 0,
            duration: dur,
            onComplete: () => {
                this.container.visible = false;
            }
        })
    }

    show(dur) {
        dur = dur || 0.25;
        this.container.visible = true;
        gsap.to(this.model.material, {
            opacity: 1,
            duration: dur
        })
    }

    destroy() {
        this.container.traverse(child => {
            if (child instanceof THREE.Mesh) {
                child.geometry.dispose();
                child.material.dispose();
            }
        })
        this.physics.world.removeBody(this.circleBody);
    }
}