import ObjectBase from "./ObjectBase";
import * as CANNON from 'cannon-es';
import * as THREE from 'three'

/**
 * 物品类
 */
export default class Product extends ObjectBase {
    constructor(_options) {
        super(_options);

        this.logo = _options.logo;
        this.sname = _options.name; // 资源名
        this.bname = 'product_' + this.sname + '_' + _options.index;

        this.setModel();

        this.container.name = this.container.bname = this.bname;
        this.mId = this.container.mId = _options.m_id;

        window.requestAnimationFrame(() => {
            this.setBody();
        })
    }

    setModel() {
        this.model = this.resource.items[this.sname].scene.clone();
        this.model.scale.setScalar(0.01);
        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                child.position.y += 10.2;
            }
        })
        this.container.add(this.model);

        if (this.sname == 'magicRing') {
            this.setMagicRingMaterial();
        } else if (this.sname == 'magicBox') {
            this.setMagicBoxMaterial();
        } else {
            this.setBakedMaterial(this.sname + 'BakedTexture');
        }

        this.setBottom();
        this.setBottomMaterial();
    }

    setBottom() {
        this.bottom = this.resource.items['productBottom'].scene.clone();
        this.bottom.scale.setScalar(0.01);
        this.container.add(this.bottom);
        this.bottom.position.y = 0.01;
    }

    /**
     * 必中圈用matcap材质
     */
    setMagicRingMaterial() {
        const matcapTexture = this.resource.items['matcapMustInTexture'];
        const material = new THREE.MeshMatcapMaterial({
            matcap: matcapTexture,
        })

        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                if (child.name.indexOf('Dummy') > -1) {
                    child.visible = false;
                } else {
                    child.material = material;
                }
            }
        });
    }

    setMagicBoxMaterial() {
        const bakedTexture = this.resource.items['magicBoxBakedTexture'];
        bakedTexture.flipY = false;
        bakedTexture.encoding = THREE.sRGBEncoding;

        const modelMaterial = new THREE.MeshBasicMaterial({
            map: bakedTexture,
        })

        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                if (child.name.indexOf('Dummy') > -1) {
                    child.visible = false;
                } else {
                    child.material = modelMaterial;
                }
            }
        });
    }

    setBottomMaterial() {
        if (this.bottom) {
            const bottomBakedTexture = this.resource.items['productBottomBakedTexture'];
            bottomBakedTexture.flipY = false;
            bottomBakedTexture.encoding = THREE.sRGBEncoding;
            const bottomBakedMaterial = new THREE.MeshBasicMaterial({
                map: bottomBakedTexture
            })

            const logoDefaultMaterial = new THREE.MeshBasicMaterial({
                color: 0xffffff
            })

            this.bottom.traverse(child => {
                if (child instanceof THREE.Mesh) {
                    if (child.name.indexOf('Dummy') > -1) {
                        child.visible = false;
                    } else if (child.name == "Brand_Logo") {
                        this.logoMesh = child;
                        child.material = logoDefaultMaterial;
                    } else if (child.name == 'Brand_Box') {
                        child.material = bottomBakedMaterial;
                    }
                }
            });
        }
    }

    /**
     * logo贴图异步加载
     */
    setLogoMaterial() {
        if (this.logoMesh && this.logo !== "") {
            const logoTexture = this.resource.items[this.logo + 'Texture'];
            logoTexture.flipY = false;
            logoTexture.encoding = THREE.sRGBEncoding;

            const logoMaterial = new THREE.MeshBasicMaterial({
                map: logoTexture
            })

            this.logoMesh.material = logoMaterial;
            this.logoMesh.material.needsUpdate = true;
        }
    }
}