import * as CANNON from 'cannon-es';
import * as THREE from 'three'
import {
    gsap
} from 'gsap'

export default class Controls {
    constructor(_options) {
        this.renderer = _options.render;
        this.time = _options.time;
        this.sizes = _options.sizes;
        this.resource = _options.resource;
        this.physics = _options.physics;
        this.productsContainer = _options.productsContainer;
        this.backwall = _options.backwall;
        this.ferrule = _options.ferrule;
        this.arrow = _options.arrow;
        this.camera = _options.camera;
        this.world = _options.world;
        this.event = _options.event;

        this.ferruleType = CustomConst.FerruleConst.NORMAL;
        this.sceneType = CustomConst.SceneConst.BABY;

        this.forceMin = 35; // 最小力度
        this.forceMax = 108; // 最大力度
        this.forceAccelerationInit = 1; // 力度变化值（绝对值）
        this.cameraRotation = 0; // 摄像头转向角度
        this.lastForce = 0; // 上一句的力度，用于记录红线

        this.force = this.forceMin; // 当前力度
        this.forceAcceleration = this.forceAccelerationInit; // 力度变化值

        this.lastCollisionInfo = []; // 上两个碰撞到道具时的碰撞信息

        this.touchStartX = 0; // 用于记录手指滑动，防止长按与转向冲突
        this.pendulumCount = 0; // 轻微摇摆状态计数

        this.resultChecking = false; // 结算锁定
        this.isPlaying = false; // 是否在游戏过程中（从圈飞出，到下一局圈重置之间）
        this.isMoving = false; // 套圈是否处于移动过程中
        this.isMovingDuration = 0.3; // 相机移动的缓动时间

        this.lastMovingX = null;

        this.mustHitCheck = false; // 必中圈射线检测
        this.mustHitRaycaster = new THREE.Raycaster();

        // 存储相机与套圈的相对距离
        this.cameraOffsetPosition = new THREE.Vector3(
            this.ferrule.container.position.x - this.camera.instance.position.x,
            this.ferrule.container.position.y - this.camera.instance.position.y,
            this.ferrule.container.position.z - this.camera.instance.position.z,
        )
        this.cameraOffsetY = 0;

        this.canvas = document.querySelector('.webgl');
        this.resultTxt = document.querySelector('.result-txt');
        this.forceBorder = document.querySelector('.force-wrapper');
        this.forceBar = document.querySelector('.force');
        this.lastForceBar = document.querySelector('.last');

        // 结算射线，记录最近碰撞到的两个物体，由商品模型的底部中心点发出，方向竖直向上，射线与圈相交，则算作套中
        this.resultRaycasterArr = [];
        // 用于检测与射线相交的圆柱（使用二维的圆会存在检测不到的情况）
        this.resultCircle = new THREE.Mesh(
            new THREE.CylinderGeometry(this.ferrule.radius, this.ferrule.radius, 0.01),
            new THREE.MeshBasicMaterial({
                color: 0x00ffff
            })
        );
        /**圆柱和套圈的初始朝向不同（圆柱竖直的，圆是Z轴水平的），因此要做旋转处理 */
        this.resultCircle.geometry.applyMatrix4(new THREE.Matrix4().makeRotationX(Math.PI / 2));
        this.resultCircle.visible = false;
        this.resultCircle.position.set(-100, -100, 0);
        this.world.container.add(this.resultCircle);

        this.setEvents();

        if (!window.isEgret) {
            // setInterval(() => {
            //     this.sceneType = (this.sceneType % 3) + 1
            // }, 3000)
            const id = this.resource.sceneLoadArr[0];
            EventBridge.trigger(BridgeEventConst.CHANGE_SCENE, {
                scene_id: id
            });
            EventBridge.trigger(BridgeEventConst.REFRESH_PRODUCTS, CustomConst['InitProducts_' + id]);

            // setTimeout(() => {
            //     EventBridge.trigger(BridgeEventConst.SCENE_LOAD_3D, {scene: 1});
            //     EventBridge.on(BridgeEventConst.SCENE_LOAD_END, () => {
            //         console.log('scenentyep')
            //         this.sceneType = 1;
            //         EventBridge.trigger(BridgeEventConst.CHANGE_SCENE, {
            //             scene_id: 1
            //         });
            //         EventBridge.trigger(BridgeEventConst.REFRESH_PRODUCTS, CustomConst['InitProducts_1']);
            //     });
            // }, 6000);
        }
    }

    setEvents() {
        // 唤起和结束游戏
        EventBridge.on(BridgeEventConst.AWAKE, this.awake.bind(this));
        EventBridge.on(BridgeEventConst.FORCE_STOP, this.forceStop.bind(this));

        // 锁定/解锁手势操作
        EventBridge.on(BridgeEventConst.LOCK, this.lock.bind(this));
        EventBridge.on(BridgeEventConst.UNLOCK, this.unlock.bind(this));

        /**切换场景 */
        EventBridge.on(BridgeEventConst.CHANGE_SCENE, this.changeScene.bind(this));
        /**刷新产品列表 */
        EventBridge.on(BridgeEventConst.REFRESH_PRODUCTS, this.refreshProducts.bind(this));
        /**切换套圈 */
        EventBridge.on(BridgeEventConst.CHANGE_FERRULE, e => {
            this.changeFerrule(e.type)
        });

        this.touchStartListener = this.touchStart.bind(this);
        this.touchEndListener = this.touchEnd.bind(this);
        this.touchMoveListener = this.touchMove.bind(this);

        /**圈的碰撞回调 */
        this.ferrule.circleBody.addEventListener('collide', this.ferruleCollide.bind(this), false);

        /**摄像头转向 */
        this.event.on(EventConst.CAMERA_ROTATE, this.cameraRotate.bind(this));

        this.onTickHandler();

        if (window.isEgret) {
            this.forceStop();
        } else {
            EventBridge.trigger(BridgeEventConst.AWAKE, {
                remaining: 3
            });
        }
    }

    initTouchEvent() {
        this.removeTouchEvent();

        if (window.isEgret) {
            window.touchLayer.addEventListener('touchBegin', this.touchStartListener, this);
            window.touchLayer.addEventListener('touchEnd', this.touchEndListener, this);
            window.touchLayer.addEventListener('touchCancel', this.touchEndListener, this);
            window.touchLayer.addEventListener('touchMove', this.touchMoveListener, this);
            window.touchLayer.touchEnabled = true;
        } else {
            this.canvas.addEventListener('touchstart', this.touchStartListener);
            this.canvas.addEventListener('touchend', this.touchEndListener);
            this.canvas.addEventListener('touchcancel', this.touchEndListener);
            this.canvas.addEventListener('touchmove', this.touchMoveListener);
            this.canvas.style.pointerEvents = 'visible';
        }
    }

    removeTouchEvent() {
        if (window.isEgret) {
            window.touchLayer.removeEventListener('touchBegin', this.touchStartListener, this);
            window.touchLayer.removeEventListener('touchEnd', this.touchEndListener, this);
            window.touchLayer.removeEventListener('touchCancel', this.touchEndListener, this);
            window.touchLayer.removeEventListener('touchMove', this.touchMoveListener, this);
            window.touchLayer.touchEnabled = false;
        } else {
            this.canvas.removeEventListener('touchstart', this.touchStartListener);
            this.canvas.removeEventListener('touchend', this.touchEndListener);
            this.canvas.removeEventListener('touchcancel', this.touchEndListener);
            this.canvas.removeEventListener('touchmove', this.touchMoveListener);
            this.canvas.style.pointerEvents = 'none';

            // this.canvas.removeEventListener('mousedown', this.touchStartListener);
            // this.canvas.removeEventListener('mouseup', this.touchEndListener);
            // this.canvas.removeEventListener('mousemove', this.touchMoveListener);
        }
    }

    /**
     * 启动游戏
     */
    awake(e) {
        e = e || {};
        this.remainTimes = e.remaining || 3;
        this.arrow.show();
        this.resetGame(true);
    }

    /**
     * 退出游戏
     */
    forceStop() {
        this.isPlaying = false;
        this.arrow.hide();
        this.forceBorder.style.opacity = '0';
        this.ferrule.hide(0.7);
        this.lock();
        this.camera.resetRotation();

        setTimeout(() => {
            if (this.ferruleType !== CustomConst.FerruleConst.NORMAL) {
                this.changeFerrule(CustomConst.FerruleConst.NORMAL);
                this.arrow.hide();
            }
        }, 1000)
    }

    /**
     * 禁止操作
     */
    lock() {
        this.removeTouchEvent();
    }

    /**
     * 允许操作
     */
    unlock() {
        this.initTouchEvent();
    }

    /**
     * 刷新商品
     * @param {商品信息}} models 
     */
    refreshProducts(models) {
        // debug
        models = CustomConst['InitProducts_' + this.sceneType];

        this.productsContainer.refreshProducts(models);
    }

    touchStart(e) {
        /**禁用选中等默认操作 */
        e.preventDefault();
        e.stopPropagation()

        this.touchStartX = e.stageX || e.touches[0].clientX;
        this.resultChecking = false;

        if (this.ferruleType !== CustomConst.FerruleConst.MUSTHIT) { // 必中圈无需发射
            /**延迟650ms, 防止跟镜头转向操作冲突 */
            this.startDelay = setTimeout(() => {
                console.log('开始蓄力')
                this.beginTouchTime = Date.now();
                this.startDelay = null;
            }, 200);
        } else {
            this.mustHitCheck = true;
        }

    }

    touchEnd(e) {
        e.preventDefault();
        e.stopPropagation()

        if (this.startDelay) { // 取消丢圈
            clearTimeout(this.startDelay);
            this.startDelay = null;
        } else if (this.beginTouchTime) {
            this.shoot();
        }

        if (this.mustHitCheck) {
            this.mustHitCheck = false;

            this.checkMustHit(e);
        }

        this.resetForce();
    }

    touchMove(e) {
        const touchX = (e.stageX || e.touches[0].clientX);
        const offsetX = touchX - this.touchStartX;

        if (Math.abs(offsetX) > 5) {
            if (this.startDelay) {
                clearTimeout(this.startDelay);
                this.startDelay = null;
                this.beginTouchTime = null;
            }

            /**控制左右移动摄像头时套圈的惯性 */
            if (this.lastMovingX !== null) {
                if (touchX - this.lastMovingX > 7) {
                    this.ferrule.moveRight();
                } else if (touchX - this.lastMovingX < -7) {
                    this.ferrule.moveLeft();
                }
            }
            this.lastMovingX = touchX;
            clearTimeout(this.movingInertiaTime);
            this.movingInertiaTime = setTimeout(() => {
                this.lastMovingX = null;
                this.ferrule.moveRecover()
            }, 60)

            this.mustHitCheck = false;
        }
    }

    onTickHandler() {
        this.time.on(EventConst.TICK, () => {
            /**抛圈力度变化 */
            if (this.beginTouchTime) {
                if (this.force < this.forceMin || this.force > this.forceMax) {
                    this.forceAcceleration *= -1;
                }
                this.force += this.forceAcceleration;

                this.forcePercent = Math.max((this.force - this.forceMin) / (this.forceMax - this.forceMin), 0);
                this.forcePercent = Math.min(this.forcePercent, 1);

                // 向UI发送力度改变时间
                if (window.isEgret) {
                    EventBridge.trigger(BridgeEventConst.POWER_CHANGE, {
                        power: this.forcePercent
                    });
                } else {
                    this.forceBar.style.transform = `scaleY(${this.forcePercent})`;
                }
            }

            /** 碰撞开始后，检测圈静止下来的逻辑 */
            if (this.forceResetFlag && !this.resultChecking) {
                /**与上一帧的相对位置变化 */
                this.ferruleOffsetVector = this.ferrule.circleBody.position.vsub(this.ferrule.circleBody.previousPosition);
                if (this.ferruleOffsetVector.almostZero(1e-5)) {
                    if (!this.resetFlag) {
                        this.resetFlag = setTimeout(() => {
                            this.checkGameResult(1);
                        }, 1000)
                    }
                } else {
                    /**可能存在短时间静止，此时应该继续进行监测 */
                    if (this.resetFlag && this.pendulumCount < 2) {
                        // console.log('-this.resetFlag', this.resetFlag)
                        clearTimeout(this.resetFlag);
                        this.resetFlag = null;
                        this.pendulumCount++; // 如果出现反复取消，则可能是处于轻微钟摆状态中，此时也可直接结束
                    }
                }
            }

            if (this.isMoving) {
                /**摄像机跟随 */
                if (!this.cameraOffsetY) {
                    this.cameraOffsetY = 0.01;
                    gsap.to(this, {
                        'cameraOffsetY': 0.35,
                        duration: this.isMovingDuration
                    })

                    gsap.to(this.camera.instance.rotation, {
                        'y': 0,
                        duration: this.isMovingDuration
                    })

                    this.cameraPosition = {
                        x: this.camera.instance.position.x,
                        y: this.camera.instance.position.y,
                        z: this.camera.instance.position.z,
                    }
                }

                gsap.killTweensOf(this.cameraPosition);
                gsap.to(this.cameraPosition, {
                    'x': this.ferrule.container.position.x - this.cameraOffsetPosition.x,
                    'y': this.ferrule.container.position.y - this.cameraOffsetPosition.y - this.cameraOffsetY,
                    'z': this.ferrule.container.position.z - this.cameraOffsetPosition.z,
                    duration: this.isMovingDuration - 0.05,
                    onUpdate: () => {
                        this.camera.instance.position.set(
                            this.cameraPosition.x,
                            this.cameraPosition.y,
                            this.cameraPosition.z,
                        );
                    }
                })
            }
        })
    }

    /**
     * 圈发射
     */
    shoot(selectedProduct) {
        console.log('force--', this.force)
        console.log('this.cameraRotation', this.cameraRotation)
        // debug
        // this.force = 87;
        // this.cameraRotation = -0.0006;

        this.isPlaying = true;
        this.isMoving = true;
        this.remainTimes--;
        // this.canvas.style.pointerEvents = 'none';
        this.removeTouchEvent();
        this.event.trigger(EventConst.SHOOT);
        EventBridge.trigger(BridgeEventConst.SHOOT);

        this.arrow.hide();
        if (this.ferruleType !== CustomConst.FerruleConst.MUSTHIT) {
            this.isMovingDuration = 0.3;
            this.forceBorder.style.opacity = '0';
            this.ferrule.applyForce(new CANNON.Vec3(-Math.tan(this.cameraRotation) * 1.5 * this.force, this.force * 0.15, -this.force));
            this.lastForce = this.forcePercent;
        } else {
            this.isMovingDuration = 0.8;
            this.ferrule.applyForce(new CANNON.Vec3(0, 0, 0));
            this.ferrule.teleportTo(selectedProduct.position);
            this.ferrule.circleBody.position.y = 0.35;
        }
    }

    /**
     * @param {*} collision 圈的碰撞信息
     * @returns 
     */
    ferruleCollide(collision) {
        if (this.resultChecking) {
            return;
        }

        /**第一次碰撞5s后还没有结果的话就强制结束 */
        if (!this.forceResetFlag) {
            this.ferrule.cancelForce();
            this.forceResetFlag = setTimeout(() => {
                this.checkGameResult(3);
            }, 5000)
        }

        const bjName = collision.contact.bj.name;
        if (bjName) {
            if (bjName.indexOf('product') > -1) { // 记录碰撞到的商品
                if (this.lastCollisionInfo.length == 0) {
                    this.lastCollisionInfo.unshift(collision.contact.bj)
                } else if (this.lastCollisionInfo.length == 1 && this.lastCollisionInfo[0].name !== bjName) {
                    this.lastCollisionInfo.unshift(collision.contact.bj)
                } else if (this.lastCollisionInfo.length == 2 && this.lastCollisionInfo[0].name !== bjName && this.lastCollisionInfo[1].name !== bjName) {
                    this.lastCollisionInfo.pop();
                    this.lastCollisionInfo.unshift(collision.contact.bj)
                }
            } else if (bjName.indexOf('LeftWall') > -1 || bjName.indexOf('RightWall') > -1) {
                /**碰撞到左右墙壁，则直接结算 */
                this.checkGameResult(2);
            }
        }
    }

    /**
     * 镜头左右转向
     * @param {*} rotationY 
     */
    cameraRotate(rotationY) {
        /**箭头位置和指向 */

        if (!this.isPlaying) {
            this.cameraRotation = rotationY;

            this.arrow.container.position.x = -Math.tan(rotationY) * 32.5;
            this.arrow.container.rotation.y = rotationY * 5;

            this.ferrule.container.position.x = -Math.tan(rotationY) * 5.6;
            this.ferrule.circleBody.position.x = -Math.tan(rotationY) * 5.6;
        }
    }

    /**
     * 必中圈检测
     */
    checkMustHit(e) {
        let mouse = {};

        if (window.isEgret) {
            mouse = {
                x: (e.stageX / stageW) * 2 - 1,
                y: -(e.stageY / stageH) * 2 + 1,
            }
        } else {
            mouse = {
                x: (e.changedTouches[0].clientX / window.innerWidth) * 2 - 1,
                y: -(e.changedTouches[0].clientY / window.innerHeight) * 2 + 1,
            }
        }

        const intersectsObjects = []
        this.productsContainer.container.traverse(child => {
            if (child instanceof THREE.Mesh) {
                if (child.name.indexOf('Dummy') == -1) {
                    intersectsObjects.push(child);
                }
            }
        })

        this.mustHitRaycaster.setFromCamera(mouse, this.camera.instance);

        const intersects = this.mustHitRaycaster.intersectObjects(intersectsObjects);
        if (intersects.length > 0) {
            const searchProduct = (obj) => {
                if (obj.bname) {
                    return obj;
                } else if (obj.parent) {
                    return searchProduct(obj.parent);
                } else {
                    return null;
                }
            }
            const selectedProduct = searchProduct(intersects[0].object);
            if (selectedProduct && selectedProduct.bname && !this.isPlaying) {
                // 选中道具
                this.shoot(selectedProduct);
            }
        }
    }

    /**
     * 检测套圈结果
     */
    checkGameResult(ori) {
        console.log('ori', ori)
        if (this.resultChecking) {
            return;
        }
        console.log('检测结果...');

        this.resultChecking = true;

        clearTimeout(this.forceResetFlag);
        this.forceResetFlag = null;

        // console.log('---this.resetFlag', this.resetFlag)
        clearTimeout(this.resetFlag);
        this.resetFlag = null;

        if (this.lastCollisionInfo.length == 0) { // 从头到尾都没碰到商品
            this.showGameResult(false);
            return;
        }

        this.resultCircle.quaternion.copy(this.ferrule.circleBody.quaternion);
        this.resultCircle.position.copy(this.ferrule.circleBody.position);
        this.resultCircle.updateWorldMatrix();

        // 生成最近碰撞的两个物体的射线
        for (let info of this.lastCollisionInfo) {
            const productPosition = info.position;
            const raycast = new THREE.Raycaster(
                new THREE.Vector3(productPosition.x, 0, productPosition.z),
                new THREE.Vector3(0, 1, 0)
            );
            this.resultRaycasterArr.push(raycast)

            if (debug) {
                this.world.container.add(new THREE.ArrowHelper(raycast.ray.direction, raycast.ray.origin, 300, 0xff0000));
            }
        }

        // 延迟检测结果
        setTimeout(() => {
            for (let i = 0; i < this.resultRaycasterArr.length; i++) {
                const raycast = this.resultRaycasterArr[i];
                // console.log('ray_' + i, raycast.ray.origin)
                const intersects = raycast.intersectObject(this.resultCircle);
                if (intersects.length > 0) {
                    this.showGameResult(true, i);
                    return;
                } else if (i == this.resultRaycasterArr.length - 1) {
                    this.showGameResult(false);
                }
            }
        }, 100)
    }

    // 显示游戏结果
    showGameResult(isBingo, resultIndex) {
        if (isBingo) {
            console.log('套中物品');
            // console.log(this.lastCollisionInfo[resultIndex]);
            this.lastForce = 0; // 套中则取消引导
            if (window.isEgret) {
                EventBridge.trigger(BridgeEventConst.SHOOT_RESULT, {
                    mId: this.lastCollisionInfo[resultIndex].mId,
                    name: this.lastCollisionInfo[resultIndex].name
                });
            } else {
                this.resultTxt.innerHTML = '套中-' + this.lastCollisionInfo[resultIndex].name.split('_')[1];
            }

        } else {
            console.log('没套中')
            EventBridge.trigger(BridgeEventConst.SHOOT_RESULT, {
                mId: ''
            });
            this.resultTxt.innerHTML = '没套中';
        }

        this.resultTxt.style.opacity = "1";
        setTimeout(() => {
            this.resultTxt.style.opacity = "0";
        }, 1500)


        this.resetGame();
    }

    /**
     * 重置力度
     */
    resetForce() {
        this.force = this.forceMin;
        this.forceAcceleration = this.forceAccelerationInit;

        this.beginTouchTime = null;
        this.pendulumCount = 0;
        this.forcePercent = 0;

        if (this.lastForce) {
            this.lastForceBar.style.opacity = '0.3';
            this.lastForceBar.style.bottom = (this.lastForce * 100) + '%';
        } else {
            this.lastForceBar.style.opacity = '0';
        }
    }

    /**
     * 切换场景
     * @param {SceneConst} scene_id 
     */
    changeScene(e) {
        this.backwall.change(e.scene_id);
        this.sceneType = e.scene_id;
    }

    /**
     * 改变套圈
     * @param {FerruleConst} ferruleType 套圈类型
     */
    changeFerrule(ferruleType) {
        if (this.ferruleType == ferruleType) {
            return;
        }

        this.ferruleType = ferruleType;
        this.ferrule.circleBody.removeEventListener('collide', this.ferruleCollide.bind(this));

        this.ferrule.change(ferruleType);

        if (this.ferruleType !== CustomConst.FerruleConst.MUSTHIT) {
            this.arrow.show();
        } else {
            this.arrow.hide();
        }

        // 圈的碰撞回调
        this.ferrule.circleBody.addEventListener('collide', this.ferruleCollide.bind(this), false);

        this.ferrule.shake(() => {})
    }

    /**
     * 重置套圈
     */
    resetFerrule() {
        this.ferrule.reset(-Math.tan(this.cameraRotation) * 5.6);
    }

    /**
     * 重置相机
     */
    resetCamera(isAwake) {
        gsap.killTweensOf(this.cameraPosition);
        gsap.killTweensOf(this.camera.instance.rotation);

        this.cameraOffsetY = 0;

        let duration = isAwake ? 0 : 0.75;
        this.camera.resetPosition(this.cameraRotation, duration, () => {
            this.isPlaying = false;
            this.resetFerrule();

            // debug
            // this.changeFerrule((this.ferruleType + 1) % 3)
            // this.changeFerrule(1)
            if (this.remainTimes > 0) {
                this.unlock();
            } else if (!window.isEgret) {
                // this.forceStop();
                EventBridge.trigger(BridgeEventConst.AWAKE, {
                    remaining: 3
                });
            }

            if (this.ferruleType !== CustomConst.FerruleConst.MUSTHIT) {
                this.forceBorder.style.opacity = '1';
                this.forceBar.style.transform = `scaleY(0)`;
                this.arrow.show();
            }
        });
    }

    /**
     * 重置游戏
     */
    resetGame(isAwake) {
        // this.canvas.style.pointerEvents = 'visible';

        this.resultCircle.position.set(-100, -100, 0);

        this.resultRaycasterArr.length = 0;
        this.lastCollisionInfo.length = 0;

        this.isMoving = false;
        if (!isAwake) {
            this.ferrule.hide();
        }
        this.resetCamera(isAwake);
        this.resetForce();

        this.event.trigger(EventConst.RESET);
    }
}