import * as THREE from 'three'
import * as CANNON from 'cannon-es';
import ObjectBase from './base/ObjectBase';

export default class Arrow extends ObjectBase {
    constructor(_options) {
        _options.updateMatrix = true;

        super(_options);

        this.bname = 'Arrow';
        this.setModel();
        this.setMaterial();
        this.setBody();
    }

    setModel() {
        this.model = this.resource.items['arrow'].scene;
        this.model.scale.setScalar(0.01);
        this.container.add(this.model);
    }

    setMaterial() {
        const bakedTexture = this.resource.items['arrowBakedTexture'];
        bakedTexture.flipY = false;
        bakedTexture.encoding = THREE.sRGBEncoding;
        const bakedMaterial = new THREE.MeshBasicMaterial({
            map: bakedTexture
        })

        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                child.material = bakedMaterial;
            }
        });
    }

    setBody() {
    }

    hide() {
        this.container.visible = false;
    }

    show() {
        this.container.visible = true;
    }
}