import * as THREE from 'three'
import * as CANNON from 'cannon-es';
import EventEmitter from '../../Utils/EventEmitter';

/**
 * 物体基类，不可直接调用
 */
export default class ObjectBase extends EventEmitter {
    constructor(_options) {
        super();

        this.CANNON = CANNON;

        this.renderer = _options.renderer;
        this.sizes = _options.sizes;
        this.time = _options.time;
        this.resource = _options.resource;
        this.physics = _options.phys;

        this.container = new THREE.Object3D();

        setTimeout(() => {
            if (!_options.updateMatrix) {
                this.container.matrixAutoUpdate = false;
            }
        }, 100);
    }

    /**设置模型 */
    setModel() {}

    /**设置3d材质 */
    setMaterial() {
        const material = new THREE.MeshStandardMaterial({
            color: '#d6eddc'
        })

        this.container.traverse(child => {
            if (child instanceof THREE.Mesh) {
                child.material = material;
                if (child.name.indexOf('Dummy') > -1) {
                    child.visible = false;
                }
            }
        });
    }

    setBakedMaterial(bakedTextureName) {
        let modelMaterial = null;
        const bakedTexture = this.resource.items[bakedTextureName];
        if (bakedTextureName && bakedTexture) {
            bakedTexture.flipY = false;
            bakedTexture.encoding = THREE.sRGBEncoding;
            modelMaterial = new THREE.MeshBasicMaterial({
                map: bakedTexture
            })
        } else {
            modelMaterial = new THREE.MeshStandardMaterial({
                color: '#d6eddc'
            })
        }

        this.model.traverse(child => {
            if (child instanceof THREE.Mesh) {
                if (child.name.indexOf('Dummy') > -1) {
                    child.visible = false;
                } else {
                    child.material = modelMaterial;
                }
            }
        });
    }


    /**设置碰撞体 */
    setBody() {
        this.body = new CANNON.Body({
            mass: 0
        });
        this.body.material = this.physics.defaultMaterial;

        this.container.traverse(child => {
            if (child instanceof THREE.Mesh && child.name.indexOf('Dummy') > -1) {
                let shape = null;
                if (child.name.indexOf('Box') > -1) {
                    shape = new CANNON.Box(new CANNON.Vec3(this.model.scale.x * child.scale.x, this.model.scale.y * child.scale.y, this.model.scale.z * child.scale.z));
                } else if (child.name.indexOf('Sphere') > -1) {
                    shape = new CANNON.Sphere(this.model.scale.x * child.scale.x);
                } else if (child.name.indexOf('Cylinder') > -1) {
                    shape = new CANNON.Cylinder(this.model.scale.x * child.scale.x, this.model.scale.x * child.scale.x, this.model.scale.y * child.scale.y, 8);
                }

                if (shape) {
                    this.body.addShape(shape, new CANNON.Vec3(
                        child.position.x * this.model.scale.x,
                        child.position.y * this.model.scale.y,
                        child.position.z * this.model.scale.z,
                    ), new CANNON.Quaternion().copy(child.quaternion));
                }
            }
        });

        this.body.position.copy(this.container.position);
        this.body.quaternion.copy(this.container.quaternion);
        this.body.name = this.bname;
        this.body.mId = this.mId;
        this.physics.world.addBody(this.body);
    }
}