import * as THREE from 'three'
import * as CANNON from 'cannon-es';
import ObjectBase from './base/ObjectBase';

export default class BackWall extends ObjectBase {
    constructor(_options) {
        super(_options);

        this.bname = 'BackWall';
        this.setModel();
        this.setMaterial(this.resource.sceneLoadArr[0]);
        this.setBody();
    }

    setModel() {
        this.model = this.resource.items['backwall'].scene;
        this.model.scale.setScalar(0.01);
        this.container.add(this.model);

        // this.container.position.z -= 2;
        // this.container.position.y -= 0.1;
        // this.container.scale.setScalar(0.8)

    }

    setMaterial(sceneType) {
        const texture = this.resource.items[`backwall${sceneType}MapTexture`];
        texture.flipY = false;
        texture.encoding = THREE.sRGBEncoding;
        const backwallMaterial = new THREE.MeshBasicMaterial({
            map: texture
        })
        this.model.children[0].material = backwallMaterial;
    }

    setBody() {
        const shape = new CANNON.Plane();

        // back
        this.backBody = new CANNON.Body({
            mass: 0,
            shape: shape
        })
        // this.backBody.position.set(0, 0, -0.63);
        this.backBody.position.set(0, 0, -2);
        this.backBody.name = "BackWall";
        this.physics.world.addBody(this.backBody);

        // left
        this.leftBody = new CANNON.Body({
            mass: 0,
            shape: shape
        })
        this.leftBody.quaternion.setFromAxisAngle(new CANNON.Vec3(0, -1, 0), -Math.PI / 2);
        this.leftBody.position.set(-1.3, 0, 0);
        this.leftBody.name = "LeftWall";
        this.physics.world.addBody(this.leftBody);

        // right
        this.rightBody = new CANNON.Body({
            mass: 0,
            shape: shape
        })
        this.rightBody.quaternion.setFromAxisAngle(new CANNON.Vec3(0, 1, 0), -Math.PI / 2);
        this.rightBody.position.set(1.3, 0, 0);
        this.rightBody.name = "RightWall";
        this.physics.world.addBody(this.rightBody);
    }

    change(sceneType) {
        this.setMaterial(sceneType);
    }
}