import * as THREE from 'three'
import ConveyorBelt from './ConveyorBelt';
import Ferrule from './Ferrule';
import Ground from './Ground';
import Physics from './Physics';
import ProductsContainer from './ProductsContainer';
import Controls from './Controls';
import BackWall from './BackWall';
import Arrow from './Arrow';

export default class World {
    constructor(_options) {
        this.renderer = _options.renderer;
        this.sizes = _options.sizes;
        this.time = _options.time;
        this.resource = _options.resource;
        this.event = _options.event;
        this.camera = _options.camera;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.resource.on(EventConst.RES_READY, this.onResReady.bind(this));
    }

    onResReady() {
        this.setPhysics();

        // this.setGuide();
        // this.setLight();
        this.setGround();
        this.setBackWall();
        this.setBelt();
        this.setProductsLayer();
        this.setFerrule();
        this.setArrow();
        this.setControls();
    }

    setGuide() {
        this.guideImg = this.resource.items['guide'];
        document.body.appendChild(this.guideImg);
        this.guideImg.style.cssText += 'position: fixed;width: 100vw;height: 100vh;';
        this.guideImg.addEventListener('touchstart', () => {
            this.guideImg.style.display = 'none';
        })
    }

    /**
     * 地面
     */
    setGround() {
        this.ground = new Ground({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            phys: this.physics,
        })
        this.container.add(this.ground.container);
    }

    /**
     * 背景墙
     */
    setBackWall() {
        this.backwall = new BackWall({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            phys: this.physics,
        })
        this.container.add(this.backwall.container);
    }

    /**
     * 传送带
     */
    setBelt() {
        for (let i = 0; i < 4; i++) {
            const belt = new ConveyorBelt({
                renderer: this.renderer,
                time: this.time,
                sizes: this.sizes,
                resource: this.resource,
                phys: this.physics,
                index: i
            })
            this.container.add(belt.container);
        }
    }

    /**
     * 商品
     */
    setProductsLayer() {
        this.productsContainer = new ProductsContainer({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            phys: this.physics,
        })
        this.container.add(this.productsContainer.container);
    }

    /**
     * 光线
     */
    setLight() {
        const parameter = {
            directionalColor: '#ffffff',
            ambientColor: '#ffffff'
        }

        const hemisphereLight = new THREE.HemisphereLight('#ffffff', '#000000', 2.69)
        hemisphereLight.position.set(0, 10, 0)
        this.container.add(hemisphereLight)
        const hemisphereLightCameraHelper = new THREE.HemisphereLightHelper(hemisphereLight)
        this.container.add(hemisphereLightCameraHelper)

        const ambientLight = new THREE.AmbientLight('#ffffff', 0.075)
        this.container.add(ambientLight)

        if (debug) {
            const debugFolder = gui.addFolder('light');
            debugFolder.add(hemisphereLight, 'intensity').min(0).max(10).step(0.001).name('平行光强度')
            debugFolder.add(hemisphereLight.position, 'x').min(-5).max(5).step(0.001).name('平行光X坐标')
            debugFolder.add(hemisphereLight.position, 'y').min(-5).max(10).step(0.001).name('平行光Y坐标')
            debugFolder.add(hemisphereLight.position, 'z').min(-5).max(5).step(0.001).name('平行光Z坐标')

            debugFolder.addColor(parameter, 'ambientColor').name('环境光颜色').onChange(() => {
                ambientLight.color.set(parameter.ambientColor);
            })
            debugFolder.add(ambientLight, 'intensity').min(0).max(2).step(0.0001).name('环境光强度')
        }
    }

    /**
     * 设置圈
     */
    setFerrule() {
        this.ferrule = new Ferrule({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            phys: this.physics,
            isInit: true
        })
        this.container.add(this.ferrule.container);
        this.ferrule.initStatus();
    }

    /**
     * 设置箭头
     */
    setArrow() {
        this.arrow = new Arrow({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            phys: this.physics,
        })
        this.container.add(this.arrow.container);
    }

    /**
     * 物理引擎
     */
    setPhysics() {
        this.physics = new Physics({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            scene: this.container
        })
    }

    /**
     * 场景控制
     */
    setControls() {
        this.controls = new Controls({
            renderer: this.renderer,
            time: this.time,
            sizes: this.sizes,
            resource: this.resource,
            physics: this.physics,
            ferrule: this.ferrule,
            arrow: this.arrow,
            productsContainer: this.productsContainer,
            camera: this.camera,
            backwall: this.backwall,
            world: this,
            event: this.event
        })
    }
}