import * as THREE from 'three'
import {
    OrbitControls
} from 'three/examples/jsm/controls/OrbitControls.js'
import {
    gsap
} from 'gsap'
import EventEmitter from './Utils/EventEmitter';

export default class Camera extends EventEmitter {
    constructor(_options) {
        super();

        this.canvas = _options.canvas;
        this.renderer = _options.renderer;
        this.sizes = _options.sizes;
        this.time = _options.time;
        this.event = _options.event;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.origin = new THREE.Vector3(0, 1.284, 11.784);
        this.target = new THREE.Vector3(0, 0, 0);

        this.cameraRotateY = 0;

        this.rotateRange = [-0.048, 0.048]

        this.setInstance();
        this.setOrbitControls();
    }

    setInstance() {
        this.instance = new THREE.PerspectiveCamera(14.805, this.sizes.viewport.width / this.sizes.viewport.height, 2.5, 14.4);
        this.instance.position.copy(this.origin); //.multiplyScalar(10)

        this.instance.up.set(0, 1, 0);
        this.container.add(this.instance);

        window.requestAnimationFrame(() => {
            this.instance.rotation.x = -Math.PI * 5 / 180;
            // this.instance.updateProjectionMatrix();
        })

        if (debug) {
            const debugFolder = gui.addFolder('camera');
            debugFolder.add(this.instance, 'aspect').max(5).min(0.1).step(0.001).onChange(() => {
                this.instance.updateProjectionMatrix()
            });
            debugFolder.add(this.instance, 'fov').max(135).min(0).step(1).onChange(() => {
                this.instance.updateProjectionMatrix()
            });
            debugFolder.add(this.instance.position, 'y').max(10).min(1).step(0.01).onChange(() => {
                // this.instance.updateProjectionMatrix()
            });
            debugFolder.add(this.instance.position, 'z').max(50).min(10).step(0.01).onChange(() => {
                // this.instance.updateProjectionMatrix()
            });
            debugFolder.add(this.instance.rotation, 'x').max(Math.PI / 2).min(-Math.PI / 2).step(0.0001).name('rotationX').onChange(() => {
                // this.instance.updateProjectionMatrix()
            });
            debugFolder.add(this.target, 'z').max(20).min(-20).step(0.01).name('targetZ').onChange(() => {
                this.instance.lookAt(this.target)
                // this.instance.updateProjectionMatrix()
            });
            this.debugFolder = debugFolder;
        }

        this.sizes.on(EventConst.RESIZE, () => {
            this.instance.aspect = this.sizes.viewport.width / this.sizes.viewport.height + 0.02
            this.instance.updateProjectionMatrix()
        })

        this.time.on(EventConst.TICK, () => {
            if (debug && this.orbitControls.enabled) {
                this.orbitControls.update();
            }
        })
    }

    setOrbitControls() {
        // Debug
        if (debug) {
            // Set up
            this.orbitControls = new OrbitControls(this.instance, this.renderer.domElement);
            this.orbitControls.enabled = true;
            this.orbitControls.enableKeys = false;
            this.orbitControls.zoomSpeed = 0.5;
            this.orbitControls.enableDamping = true;

            this.debugFolder.add(this.orbitControls, 'enabled').name('orbitControlsEnabled');
        }

        if (window.isEgret) {
            window.touchLayer.addEventListener('touchBegin', this.onTouchStart.bind(this), false);
            window.touchLayer.addEventListener('touchMove', this.onTouchMove.bind(this), false);
        } else {
            this.canvas.addEventListener('touchstart', this.onTouchStart.bind(this), false);
            this.canvas.addEventListener('touchmove', this.onTouchMove.bind(this), false);
            // this.canvas.addEventListener('mousedown', this.onTouchStart.bind(this));
            // this.canvas.addEventListener('mousemove', this.onTouchMove.bind(this));
        }
    }

    onTouchStart(e) {
        this.touchX = e.stageX || e.touches[0].clientX;
    }

    onTouchMove(e) {
        const offsetX = (e.stageX || e.touches[0].clientX) - this.touchX;
        this.touchX = (e.stageX || e.touches[0].clientX);

        let multi = 0.0008;
        if (!window.isEgret) {
            multi = 0.0015;
        }
        const targetRotationY = THREE.MathUtils.clamp(this.instance.rotation.y + (offsetX * multi), this.rotateRange[0], this.rotateRange[1])
        gsap.killTweensOf(this.instance.rotation)
        gsap.to(this.instance.rotation, {
            y: targetRotationY,
            duration: 0.15,
            onUpdate: () => {
                this.cameraRotateY = this.instance.rotation.y;
                this.event.trigger(EventConst.CAMERA_ROTATE, [this.cameraRotateY]);
            }
        })

        // this.cameraRotateY = targetRotationY;
        // this.event.trigger(EventConst.CAMERA_ROTATE, [targetRotationY]);
    }

    resetPosition(rotationY, duration, callback, ref) {
        this.cameraRotateY = rotationY;
        gsap.killTweensOf(this.instance.rotation);
        gsap.to(this.instance.rotation, {
            y: this.cameraRotateY,
            duration: duration,
            ease: "sine.in",
        })

        gsap.killTweensOf(this.instance.position);
        gsap.to(this.instance.position, {
            x: this.origin.x,
            y: this.origin.y,
            z: this.origin.z,
            duration: duration,
            ease: "sine.in",
            onComplete: () => {
                if (callback) {
                    callback.apply(ref);
                }
            }
        })
        // this.instance.rotation.x = -Math.PI * 5 / 180;
        // this.instance.position.copy(this.origin);
    }

    resetRotation() {
        this.cameraRotateY = 0;
        gsap.to(this.instance.rotation, {
            y: this.cameraRotateY,
            duration: 1,
            onUpdate: () => {
                this.event.trigger(EventConst.CAMERA_ROTATE, [this.instance.rotation.y]);
            }
        })
    }
}